.adminnavbar{

    display: flex;
    height: 100px;
    background-color: rgb(187, 232, 232);
    align-items: center;
    justify-content: space-around;

}
.navlinks{
    display: flex;
    width: 50%;
    justify-content: space-between;
}
.navlinks a{
    text-decoration: none;
    color: blue;
    font-size: 20px;
    font-weight: bold;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.logo{
    color: orange;
    font-size: 40px;
}
span{
    color: rgb(228, 244, 9);
    font-size: 100px;
}